import React from "react";
import styled from "styled-components";

export const Message = ({ messageContent, username }) => {
  const isOwnMessage = messageContent.author === username;
  const isNotification = !messageContent.author;

  if (isNotification) {
    return <NotificationMessage>{messageContent.message}</NotificationMessage>;
  }

  return (
    <MessageContainer isOwnMessage={isOwnMessage}>
      <MessageAuthor isOwnMessage={isOwnMessage}>
        {messageContent.author}
      </MessageAuthor>
      <MessageText isOwnMessage={isOwnMessage}>
        {messageContent.message}
      </MessageText>
      <MessageTime isOwnMessage={isOwnMessage}>
        {new Date(messageContent.time).toLocaleTimeString()}
      </MessageTime>
    </MessageContainer>
  );
};

const NotificationMessage = styled.div`
  text-align: center;
  color: #000;
  font-size: 0.9em;
  margin: 10px 0;
`;

const MessageContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: ${({ isOwnMessage }) =>
    isOwnMessage ? "flex-end" : "flex-start"};
  margin: 10px;
`;

const MessageAuthor = styled.span`
  font-weight: bold;
  color: ${({ isOwnMessage }) => (isOwnMessage ? "#ff69b4" : "#4b4b4b")};
`;

const MessageText = styled.span`
  max-width: 80%;
  background-color: ${({ isOwnMessage }) =>
    isOwnMessage ? "#ffb6c1" : "#f0f0f0"};
  color: #4b4b4b;
  padding: 10px;
  border-radius: 10px;
  margin-top: 5px;
`;

const MessageTime = styled.span`
  font-size: 0.7em;
  color: #a0a0a0;
  margin-top: 5px;
`;
