import React, { useState, useEffect } from "react";
import io from "socket.io-client";
import Chat from "./components/Chat";
import styled from "styled-components";

const socket = io.connect("http://localhost:4000");

function App() {
  const [username, setUsername] = useState("");
  const [showChat, setShowChat] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const [notification, setNotification] = useState("");
  const [isChecked, setIsChecked] = useState(false); // isChecked  

  const joinRoom = (e) => {
    e.preventDefault();
    if (username !== "") {
      //     
      if (isChecked) {
        // isChecked  
        socket.emit("join_room", { username });
        setShowChat(true);
      } else {
        setErrorMsg(
          "  ?  ."
        );
      }
    } else {
      setErrorMsg("  .");
    }
  };

  const handleLogout = () => {
    socket.emit("logout", { username });
    setShowChat(false);
    setUsername("");
  };

  useEffect(() => {
    socket.on("receive_notification", (message) => {
      setNotification(message);
      setTimeout(() => {
        setNotification("");
      }, 5000);
    });
  }, []);

  return (
    <ChatApp>
      {!showChat ? (
        <ChatContainer onSubmit={joinRoom}>
          <ChatTitle>    !</ChatTitle>
          <ChatInput
            type="text"
            placeholder="  "
            onChange={(e) => {
              setErrorMsg("");
              setUsername(e.target.value);
            }}
          />
          <CheckboxContainer>
            <Checkbox
              id="chatroom-checkbox"
              type="checkbox"
              checked={isChecked}
              onChange={(e) => setIsChecked(e.target.checked)}
            />
            <CheckboxLabel htmlFor="chatroom-checkbox">
                ?
            </CheckboxLabel>
          </CheckboxContainer>
          <ErrorMessage>{errorMsg}</ErrorMessage>
          <ChatButton type="submit"></ChatButton>
        </ChatContainer>
      ) : (
        <>
          <Chat socket={socket} username={username} onLogout={handleLogout} />
        </>
      )}
      {notification && <Notification>{notification}</Notification>}
    </ChatApp>
  );
}

export default App;

const ChatApp = styled.div`
  width: 100vw;
  height: 100vh;
  color: #4b4b4b;
  display: grid;
  place-items: center;
  font-family: "Noto Sans KR", sans-serif;
`;

const ChatContainer = styled.form`
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 10px;
  padding: 20px;
  background-color: #fff;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
`;

const ChatTitle = styled.h3`
  font-size: 1.5rem;
  margin-bottom: 20px;
  color: #ff69b4;
`;

const ChatInput = styled.input`
  width: 80%;
  height: 40px;
  margin-bottom: 10px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 10px;
  font-size: 1rem;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
`;

const CheckboxContainer = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 10px;
`;

const Checkbox = styled.input`
  margin-right: 10px;
`;

const CheckboxLabel = styled.label`
  font-size: 1rem;
  color: #4b4b4b;
`;

const ErrorMessage = styled.p`
  color: #ff4d4d;
  font-size: 0.8rem;
  margin-bottom: 10px;
`;

const ChatButton = styled.button`
  width: 80%;
  height: 50px;
  margin-top: 10px;
  border: none;
  border-radius: 10px;
  background-color: #ff69b4;
  color: #fff;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  &:hover {
    background-color: #ff3e96;
  }
`;

const Notification = styled.div`
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  padding: 10px 20px;
  background-color: #fff0f6;
  border: 1px solid #ccc;
  border-radius: 10px;
  font-size: 1rem;
  z-index: 1000;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
`;
