import React, { useEffect, useRef, useState } from "react";
import styled from "styled-components";
import { Message } from "./Message";
import { v4 as uuidv4 } from "uuid";

function Chat({ socket, username, room, onLogout }) {
  const inputRef = useRef();
  const [messageList, setMessageList] = useState([]);
  const messageBottomRef = useRef(null);

  const sendMessage = async () => {
    const currentMsg = inputRef.current.value;
    if (currentMsg !== "") {
      const messageData = {
        author: username,
        message: currentMsg,
        time: new Date().toISOString(),
        room: room,
      };
      await socket.emit("send_message", messageData);
      setMessageList((list) => [...list, messageData]);
      inputRef.current.value = "";
    }
  };

  useEffect(() => {
    socket.on("receive_message", (data) => {
      setMessageList((list) => [...list, data]);
    });
  }, [socket]);

  useEffect(() => {
    messageBottomRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messageList]);

  return (
    <ChatContainer>
      <ChatHeader>
        <ChatTitle> </ChatTitle>
        <LogoutButton onClick={onLogout}></LogoutButton>
      </ChatHeader>
      <ChatBody>
        <MessageBox>
          {messageList.map((messageContent) => (
            <Message
              messageContent={messageContent}
              username={username}
              key={uuidv4()}
            />
          ))}
          <div ref={messageBottomRef} />
        </MessageBox>
      </ChatBody>
      <ChatInputBox>
        <ChatInput
          ref={inputRef}
          type="text"
          placeholder=" "
          onKeyPress={(event) => {
            if (event.key === "Enter") {
              sendMessage();
            }
          }}
        />
        <ChatButton onClick={sendMessage}></ChatButton>
      </ChatInputBox>
    </ChatContainer>
  );
}

export default Chat;

const ChatContainer = styled.div`
  width: 50%;
  max-width: 600px;
  @media screen and (max-width: 550px) {
    width: 90%;
  }
  height: 440px;
  background-color: #fff0f6;
  border-radius: 10px;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
`;

const ChatHeader = styled.div`
  height: 40px;
  border-radius: 10px 10px 0 0;
  background: #ff69b4;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1em;
`;

const ChatTitle = styled.p`
  margin: 0;
  color: #fff;
  font-weight: 700;
  line-height: 40px;
`;

const LogoutButton = styled.button`
  background: #ff4d4d;
  border: none;
  border-radius: 5px;
  padding: 0.5em 1em;
  color: white;
  cursor: pointer;
  font-size: 0.9em;
  &:hover {
    background: #d14040;
  }
`;

const ChatBody = styled.div`
  height: 360px;
  border: 1px solid #ff69b4;
  background: #fff;
  overflow-y: auto;
`;

const MessageBox = styled.div`
  width: 100%;
  padding-top: 5px;
`;

const ChatInputBox = styled.div`
  height: 40px;
  border: 1px solid #ff69b4;
  border-top: none;
  display: flex;
  border-radius: 0 0 10px 10px;
`;

const ChatInput = styled.input`
  height: 100%;
  flex: 85%;
  border: 0;
  padding: 0 0.7em;
  font-size: 1em;
  border-right: 1px dotted #ff69b4;
  outline: none;
  background: transparent;
`;

const ChatButton = styled.button`
  border: 0;
  display: grid;
  place-items: center;
  cursor: pointer;
  flex: 15%;
  height: 100%;
  background: transparent;
  outline: none;
  font-size: 25px;
  transition: all 0.5s;
  color: lightgray;
  &:hover {
    background: #ff69b4;
    color: white;
    transition: all 0.5s;
  }
  &:active {
    background: #ff3e96;
    color: white;
    font-size: 0.5rem;
  }
`;
